            ><br /><br />
            <div class="info-box">
              <span class="desc-text">Tel. 051-301-5908</span>
              <span class="desc-text">Fax. 3030-3448-0523</span>
              <span class="desc-text">E-mail. gameinscokr@naver.com</span>
              <router-link to="/Contact" class="contact-btn"
                >문의하기 바로가기</router-link
              >
            </div>
          </div>
        </div>
        <div class="circle_box">
          <router-link to="/Contact" class="link_box">
            <img
              src="@/assets/home/contact_circle.svg"
              class="circle_img"
            /><span class="mdi mdi-chevron-right circle_arrow"></span>
          </router-link>
        </div>
      </div>
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3261.548042668115!2d128.9868924160836!3d35.1678907655588!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3568ea7f35096129%3A0x26e62be76f0388cf!2z67aA7IKw6rSR7Jet7IucIOyCrOyDgeq1rCDrsLHslpHrjIDroZwgNjg0!5e0!3m2!1sko!2skr!4v1668045718480!5m2!1sko!2skr"
