<script>
import { gsap, ScrollTrigger } from "gsap/all";
import Footer from "./../footer/Footer_Home.vue";
export default {
  name: "contact_section",
  components: {
    Footer,
  },
  mounted() {
    this.fadeInAnimation();
  },
  methods: {
    fadeInAnimation() {
      gsap.registerPlugin(ScrollTrigger);

      const contact = gsap.timeline({
        scrollTrigger: {
          trigger: ".contact_section",
          start: "top center",
          end: "bottom center",
        },
      });
      contact.to(
        ".contact_section .container .text-container .text-box .title",
        {
          top: 0,
          opacity: 1,
          duration: 1,
        },
        "start"
      );
      contact.to(
        ".contact_section .container .text-container .text-box .desc-title",
        {
          top: 0,
          opacity: 1,
          duration: 1,
        },
        "start+=.3"
      );
      contact.to(
        ".contact_section .container .text-container .text-box .desc-addr",
        {
          top: 0,
          opacity: 1,
          duration: 1,
        },
        "start+=.5"
      );
      contact.to(
        ".contact_section .container .text-container .text-box .info-box",
        {
          top: 0,
          opacity: 1,
          duration: 1,
        },
        "start+=.7"
      );
      contact.to(
        ".contact_section .container .text-container .circle_box",
        {
          left: 0,
          opacity: 1,
          duration: 1.5,
        },
        "start+=1"
      );
      contact.to(
        ".contact_section .container .googlemap",
        {
          opacity: 1,
          duration: 2,
        },
        "start+=1.2"
      );
    },
  },
};
</script>

