<template>
  <section class="second_section">
    <div class="container">
      <div class="box">
        <div class="overview">who we are</div>
        <div class="content">
          <div class="title-con">
            <p class="init-text">
              지속적으로 진화하는 <span class="blue-text">디지털 환경에서</span>
            </p>
            <p class="init-text">
              저희는 <span class="blue-text">끊임없이 혁신하고 있습니다.</span>
            </p>
            <!-- 사람의 일상과 'IT'의
              <span class="blue-text">경계를 허물어</span>
            </p>
            <p class="init-text">
              <span class="blue-text">모든 이에게 열려있는 새로운 세상</span>
              을 그려갑니다.
            </p> -->
          </div>
          <div class="desc-con">
            <div class="desc text1">
              (주)게임인스는 기술 지향적 솔루션 구축, 인터넷 플랫폼 개발에<br />
              주력하며 고객만족도를 최우선으로 하고 있습니다.<br />
              우리는 여러분을 새로운 세상의 높은 곳으로 인도하겠습니다.
              <!-- 지속적으로 진화하는 디지털 환경에서 우리는 다양한 요구 사항을<br />
              충족하기 위해 끊임없이 혁신하고 있습니다.<br /> -->
            </div>
            <div class="desc text2">
              사람과 기술이 모여 더 나은 미래를 논의하는 모든 곳에<br />
              <strong>(주)게임인스가</strong> 함께합니다.
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { gsap, ScrollTrigger } from "gsap/all";

export default {
  computed: {},
  data() {
    return {};
  },
  mounted() {
    this.scrollAnime();
  },
  methods: {
    scrollAnime() {
      gsap.registerPlugin(ScrollTrigger);

      const textLine = gsap.timeline({
        scrollTrigger: {
          trigger: ".content",
          start: "top bottom",
          end: "bottom top",
          direction: "reverse",
          // scrub: true,
          repeat: -1,
        },
        // yoyo: true,
      });

      textLine.to(
        ".init-text",
        {
          "--left": "200%",
        },
        "start+=0.3"
      );
      textLine.to(
        ".blue-text",
        {
          "--left": "0%",
        },
        "start+=1.8"
      );

      textLine.to(
        ".second_section .container .overview",
        {
          opacity: 1,
          bottom: 0,
        },
        "start"
      );

      textLine.to(
        ".second_section .container .content .title-con",
        {
          opacity: 1,
          bottom: 0,
        },
        "start+=0.3"
      );

      textLine.to(
        ".second_section .container .content .desc-con .text1",
        {
          opacity: 1,
          bottom: 0,
        },
        "start+=.8"
      );

      textLine.to(
        ".second_section .container .content .desc-con .text2",
        {
          opacity: 1,
          bottom: 0,
        },
        "start+=1"
      );
    },
  },
  setup() {},
};
</script>

<style lang="scss" scoped>
.second_section {
