  background-size: cover;
  background-position: center;
  z-index: 1;
  .container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    .box {
      display: flex;
      justify-content: space-between;
      .overview {
        position: relative;
        opacity: 0;
        bottom: -32px;
        font-family: "Pretendard";
      }
      .content {
        font-family: "Pretendard";
        .title-con {
          font-weight: bold;
          opacity: 0;
          bottom: -50px;
          .blue-text {
            position: relative;
            display: inline-flex;
            // background: linear-gradient(
            //   to bottom,
            //   transparent 20%,
            //   #0174f5 20%
            // );
            // background-size: 100% 300%;
            transition: 0.45s;
            z-index: 2;
            overflow: hidden;
            --left: -200%; /* 새로운 CSS 변수 */
            width: fit-content;
          }
          .blue-text::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: var(--left); /* CSS 변수를 사용 */
            width: 200%;
            height: 40%;
            z-index: -1;
            background-color: #0174f5;
            transition: left 0.8s ease, height 0.3s ease;
          }
          // .blue-text:hover::after {
          //   height: 100%;
          // }

          .init-text {
            position: relative;
            z-index: 2;
            overflow: hidden;
            --left: -200%; /* 새로운 CSS 변수 */
            width: fit-content;
          }
          .init-text::after {
            content: "";
            position: absolute;
            left: var(--left); /* CSS 변수를 사용 */
            width: 200%;
            align-self: center;
            z-index: 2;
            background-color: #000000;
            transition: all 2.5s ease;
          }
          // .init-text:hover::after {
          //   left: 100%;
          // }
        }
        .desc-con {
          .text1,
          .text2 {
            position: relative;
            opacity: 0;
            bottom: -50px;
          }
        }
      }
    }
  }
}

