<script>
import { mapGetters, mapActions } from "vuex";
import { gsap, ScrollTrigger } from "gsap/all";
// import Intro from "../main/1Intro_section.vue";

export default {
  name: "first_section",
  data() {
    return {
      clickIntro: false,
    };
  },
  components: {
    // Intro,
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  mounted() {
    // if (this.clickIntro) {
    window.addEventListener("wheel", this.stopEvent, {
      passive: false,
    });
    window.addEventListener("touchmove", this.stopEvent, {
      passive: false,
    });
    this.animateTitle();
    setTimeout(() => {
      window.removeEventListener("wheel", this.stopEvent);
      window.removeEventListener("touchmove", this.stopEvent);
    }, 2500);
    // this.animateDesc();
    this.animateScrollOpacity();
    this.animateScroll();
    this.scrollAnime();
    // }
  },
  methods: {
    ...mapActions(["setIsScrolling"]),
    scrollAnime() {
      gsap.registerPlugin(ScrollTrigger);
      const MainTitleTimeline = gsap.timeline({
        scrollTrigger: {
          trigger: ".first_section",
          start: "top top",
          end: "+=25%",
          scrub: 1,
        },
      });
      MainTitleTimeline.to(
        ".txLine1",
        {
          delay: 0,
          opacity: 0,
          scale: 1.2,
          letterSpacing: "0.2rem",
        },
        "start"
      );
      MainTitleTimeline.to(
        ".txLine2",
        {
          delay: 0,
          opacity: 0,
          scale: 1.2,
          letterSpacing: "0.2rem",
        },
        "start"
      );
      // MainTitleTimeline.to(
      //   ".vision_title",
      //   {
      //     delay: 0,
      //     opacity: 0,
      //     scale: 1.5,
      //     letterSpacing: "0.2rem",
      //   },
      //   "start"
      // );
    },

    animateTitle() {
      this.$anime({
        targets: ".fir_coment",
        duration: 1500,
        delay: 0,
        color: "rgba(255,255,255, 1)",
        opacity: 1,
        easing: "easeOutSine",
        top: "-5vh",
      });
      this.$anime({
        targets: ".second_coment",
        duration: 1500,
        delay: 300,
        color: "rgba(255,255,255, 1)",
        opacity: 1,
        easing: "easeOutSine",
        top: "-5vh",
      });
      this.$anime({
        targets: ".third_coment",
        duration: 1500,
        delay: 600,
        color: "rgba(255,255,255, 1)",
        opacity: 1,
        easing: "easeOutSine",
        top: "-5vh",
      });
      this.$anime({
        targets: ".fourth_coment",
        duration: 1500,
        delay: 900,
        color: "rgba(255,255,255, 1)",
        opacity: 1,
        easing: "easeOutSine",
        top: "-5vh",
      });
    },
    animateDesc() {
      this.$anime({
        targets: ".vision_dec",
        duration: 1500,
        delay: 1200,
        color: "rgba(255,255,255, 1)",
        opacity: 1,
        easing: "easeOutSine",
        top: "-5vh",
      });
    },

    animateScrollOpacity() {
      this.$anime({
        targets: ".scroll_con",
        opacity: 1,
        delay: 2000,
        duration: 1000,
        easing: "easeInOutQuad",
      });
    },
    animateScroll() {
      this.$anime({
        targets: ".down_icon",
        bottom: [-15, -5],
        duration: 600,
        easing: "easeInOutQuad",
        direction: "alternate",
        loop: true,
      });
    },
    stopEvent(e) {
      e.preventDefault();
      e.stopPropagation();
    },
    scrollHandler() {
      this.setIsScrolling(true);
    },
    setupScrollToNextSection() {
      const section = document.querySelector(".first_section");
      let isTouching = false;

      section.addEventListener(
        "touchstart",
        () => {
          isTouching = true;
        },
        { passive: true }
      );

      section.addEventListener(
        "touchmove",
        () => {
          isTouching = true;
        },
        { passive: true }
      );

      section.addEventListener(
        "touchend",
        () => {
          isTouching = false;
        },
        { passive: true }
      );

      section.addEventListener(
        "wheel",
        (e) => {
          if (!isTouching) {
            e.preventDefault();
            if (this.isScrolling) {
              return;
            }
            this.setIsScrolling(true);

            if (e.deltaY > 0 && this.isScrolling) {
              window.scrollTo({
                top: window.innerHeight,
                behavior: "smooth",
              });
            } else if (e.deltaY < 0 && this.isScrolling) {
              window.scrollTo({
                top: 0,
                behavior: "smooth",
              });
            }
            setTimeout(() => {
              this.setIsScrolling(false);
            }, 500);
          }
        },
        { passive: false }
      );
    },
  },
};
</script>

