<script>
import { gsap, ScrollTrigger } from "gsap/all";
export default {
  name: "slide_section",
  computed: {},
  data() {
    return {
      news: [
        {
          yymm: "2024.07",
          dd: "22",
          title: "2024 게임인스 하계 휴가 안내",
          idx: 47,
        },
        {
          yymm: "2024.06",
          dd: "20",
          title: "2024 고용우수기업 인증서 수여식",
          idx: 46,
        },
        {
          yymm: "2024.06",
          dd: "04",
          title: "2024 서부산권일자리박람회",
          idx: 45,
        },
        {
          yymm: "2024.05",
          dd: "29",
          title: "부산북부고용센터 구인, 구직 만남의 날",
          idx: 44,
        },
        {
          yymm: "2024.05",
          dd: "27",
          title: "2024 해운대구 일자리박람회",
          idx: 43,
        },
      ],
    };
  },
  mounted() {
    this.fadeInAnimation();
  },
  methods: {
    fadeInAnimation() {
      gsap.registerPlugin(ScrollTrigger);

      const news = gsap.timeline({
        scrollTrigger: {
          trigger: ".slide_section",
          start: "top bottom",
          end: "bottom center",
        },
      });

      if (window.innerWidth <= 760) {
        news.to(
          ".slide_section .bg",
          {
            width: "100%",
          },
          "start"
        );
      } else {
        news.to(
          ".slide_section .bg",
          {
            width: "70%",
          },
          "start"
        );
      }

      // news.to(
      //   ".slide_section .bg",
      //   {
      //     width: "70%",
      //   },
      //   "start"
      // );
      news.to(
        ".slide_section .container .title-con",
        {
          opacity: 1,
          top: 0,
        },
        "start+=1"
      );
      news.to(
        ".slide_section .container .news-con .news-box",
        {
          opacity: 1,
          top: 0,
        },
        "start+=1.2"
      );
      news.to(
        ".slide_section .container .news-con .news-card",
        {
          opacity: 1,
          left: 0,
        },
        "start+=1.4"
      );
    },
  },
};
</script>

