          color: "white",
        },
        "start"
      );
      service.to(
        ".service-section .container .card-con",
        {
          color: "white",
        },
        "start"
      );
    },
    initCardAnimations() {
      gsap.registerPlugin(ScrollTrigger);

      // Loop through each card and set up animations
      this.RTcard.forEach((card) => {
        const cardSelector = `.card${card.num}`;

        gsap.from(cardSelector, {
          opacity: 0,
          y: 200, // Adjust this value to control the initial position of the card
          scrollTrigger: {
            trigger: cardSelector,
            start: "top 80%", // Adjust this value to control when the animation starts
            end: "bottom 50%", // Adjust this value to control when the animation ends
            scrub: true,
          },
        });
      });
    },
  },
};
